header {
  position: fixed;
  top: 0;
  height: 56px;
  z-index: 9999999;
  background: var(--bg-primary);
  width: 100%;
  box-shadow: 1px 8px 15px -7px var(--shadow-2);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  color: var(--color-primary);

  /*----Header left----*/
  .circle {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .header_left {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 5px 1rem;

    .search {
      display: flex;
      align-items: center;
      gap: 8px;
      background: var(--bg-forth);
      padding: 10px 32px 10px 10px;
      border-radius: 50px;
      cursor: text;
      input {
        outline: none;
        border: none;
        background: transparent;
        font-size: 15px;
        font-family: inherit;
        &::placeholder {
          transform: translate(-1px);
        }
      }
    }
    .search1 {
      @media screen and (max-width: 1040px) {
        width: 40px;
        height: 40px;
        padding: 0;
        justify-content: center;
        input {
          display: none;
        }
      }
    }
  }
  /*----Header right ends----*/
  /*----Header middle----*/
  .header_middle {
    display: flex;
    align-items: center;
    gap: 12px;
    transform: translateX(3px);
    @media screen and (max-width: 1040px) {
      transform: translateX(-16%);
    }
    @media screen and (max-width: 850px) {
      transform: translateX(-23%);
    }
    @media screen and (max-width: 780px) {
      transform: translateX(-30%);
    }
    @media screen and (max-width: 720px) {
      transform: translateX(-30%);
    }
    @media screen and (max-width: 620px) {
      transform: translateX(-12%);
    }
    @media screen and (max-width: 505px) {
      gap: 7px;
    }

    .middle_icon {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 120px;
      height: 50px;
      border-radius: 10px;
      cursor: pointer;
      transform: translateX(-2px);
      &:nth-child(1) {
        @media screen and (max-width: 480px) {
          display: none;
        }
      }
      &:nth-child(2) {
        transform: translateX(-4px);
        @media screen and (max-width: 720px) {
          transform: translateX(7px);
        }
      }
      &:nth-child(3) {
        transform: translateX(-3px);
        @media screen and (max-width: 720px) {
          display: none;
        }
      }
      &:nth-child(4) {
        transform: translateX(-5px);
        @media screen and (max-width: 720px) {
          display: none;
        }
      }
      &:nth-child(5) {
        transform: translateX(-5px);
        @media screen and (max-width: 825px) {
          display: none;
        }
      }
      @media screen and (max-width: 1290px) {
        width: 90px;
      }
      @media screen and (max-width: 1135px) {
        width: 70px;
      }
    }
    .active {
      border-bottom: 3px solid var(--blue-color);
      border-radius: 0;
      height: 56px;
      transform: translateX(0);
      svg {
        transform: translateY(1px) translateX(-1px);
      }
    }
    .middle_notification {
      position: absolute;
      top: 3px;
      right: 1.9rem;
      background: #e41e3f;
      border-radius: 50px;
      padding: 1px 5px;
      font-size: 13px;
      color: #fff;
    }
  }
  /*----Header middle ends----*/
  /*----Header right----*/
  .header_right {
    display: flex;
    position: absolute;
    right: 8px;
    top: 50%;
    transform: translateY(-50%);

    .profile_link {
      display: flex;
      align-items: center;
      gap: 5px;
      cursor: pointer;
      padding: 3px 10px 3px 6px;
      border-radius: 50px;
      font-weight: 600;
      font-size: 14px;
      line-height: 19px;
      margin-right: 10px;

      img {
        width: 28.7px;
        height: 28.7px;
        border-radius: 50%;
        border: 1px solid #b0b3b8;
        transform: translateX(-1px);
      }
      span {
        margin-bottom: 1px;
      }
      @media screen and (max-width: 620px) {
        display: none;
      }
    }
    .circle_icon {
      position: relative;
      height: 40px;
      width: 40px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 8px;
      background: var(--bg-third);
      cursor: pointer;
      &:nth-child(3) {
        @media screen and (max-width: 400px) {
          display: none;
        }
      }

      .right_notification {
        position: absolute;
        top: -6px;
        right: -5px;
        background: #e41e3f;
        border-radius: 50px;
        padding: 1px 6px;
        font-size: 13px;
        color: #fff;
      }
    }
  }
  /*----Header left ends----*/
}
/*----Search menu----*/
.search_area {
  position: absolute;
  align-items: flex-start;
  flex-direction: column;
  gap: 6px;
  background: var(--bg-primary);
  box-shadow: 0 12px 12px var(--shadow-1), inset 0 0 0 0 var(--shadow-inset);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  transition: all 0.2s;
  -webkit-font-smoothing: antialiased;
  z-index: 1;
  min-height: 400px;
  max-height: 70vh;
  padding: 8px 16px 10px 6px;

  .search_wrap {
    display: flex;
    align-items: center;
    gap: 6px;
  }
  .search {
    padding: 10px 44px 10px 12px;
    width: 252px;
    gap: 4px;

    svg {
      transform: translateY(1px);
    }
    input {
      padding-left: 4px;
      &::placeholder {
        transform: translateY(-2px);
      }
      &:focus::placeholder {
        transform: translateY(-1px);
      }
    }
  }
  .search_history {
    width: 100%;
  }
  .search_history_header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    font-size: 14px;
    span {
      font-weight: 600;
      font-size: 16px;
    }
    a {
      cursor: pointer;
      color: var(--blue-color);
    }
  }
}
/*----Search menu ends----*/

/*---Search All Menu-----*/
.all_menu {
  position: absolute;
  right: -9rem;
  top: 45px;
  background: var(--bg-secondary);
  border-radius: 10px;
  height: 90vh;
  width: 575px;
  padding: 10px 1rem;
  box-shadow: -2px 1px 5px 2px var(--shadow-1);
  user-select: none;
  @media screen and (max-width: 620px) {
    width: 485px;
  }
  @media screen and (max-width: 505px) {
    width: 340px;
    right: -9rem;
  }
  @media screen and (max-width: 400px) {
    left: -12.5rem;
  }

  .all_menu_header {
    font-size: 24px;
    line-height: 28px;
    font-weight: 700;
    margin-bottom: 10px;
  }
  .all_menu_wrap {
    display: grid;
    grid-template-columns: 1.7fr 1fr;
    gap: 1rem;
    overflow-y: auto;
    height: 95%;
    @media screen and (max-width: 505px) {
      grid-template-columns: 1fr;
    }

    .all_left {
      background: var(--bg-primary);

      border-radius: 10px;
      padding: 15px;
      box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
      @media screen and (max-width: 620px) {
        width: 250px;
      }
      @media screen and (max-width: 505px) {
        width: 100%;
      }
    }

    .all_menu_search {
      background: var(--bg-secondary);

      display: flex;
      align-items: center;
      gap: 5px;
      padding: 10px;
      border-radius: 50px;
      input {
        border: none;
        outline: none;
        background: transparent;
        width: 100%;
        font-size: 15px;
      }
    }

    .all_menu_group {
      margin-top: 10px;
      border-bottom: 1px solid #ced0d4;
    }
    .all_menu_group_header {
      font-weight: 600;
      font-size: 16px;
      padding: 10px 0;
    }
    .all_menu_group {
      width: 100%;
    }
    .all_menu_item {
      display: flex;
      align-items: center;
      width: 100%;
      margin-bottom: 5px;
      padding: 5px;
      cursor: pointer;
      border-radius: 10px;
      img {
        width: 36px;
        height: 36px;
        margin-right: 10px;
      }
    }
    .all_menu_col {
      display: flex;
      flex-direction: column;
      span:first-of-type {
        font-size: 14px;
        font-weight: 500;
      }
      span:last-of-type {
        font-size: 12px;
        color: #65676b;
        width: 95%;
      }
    }
  }
  .all_right {
    padding: 15px 10px;
    background: var(--bg-primary);
    margin-right: 1rem;
    border-radius: 10px;
    height: 550px;
    width: 170px;
    position: fixed;
    right: 2rem;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    @media screen and (max-width: 505px) {
      position: relative;
      width: 100%;
      padding-left: 3rem;
      margin-bottom: 1rem;
    }
  }
  .all_right_header {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 15px;
  }

  .all_right_item {
    display: flex;
    align-items: center;
    font-weight: 600;
    gap: 10px;
    font-size: 14px;
    cursor: pointer;
    padding: 5px 10px;
    border-radius: 10px;
    margin-bottom: 2px;
  }

  .all_right_circle {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bg-third);
  }
}
/*---Search All Menu-----*/
/*-------User menu----*/
.mmenu {
  padding: 0 0.3rem;
  position: absolute;
  top: 100%;
  right: 0;
  width: 360px;
  border-radius: 10px;
  background: var(--bg-primary);
  box-shadow: 2px 2px 2px var(--shadow-1);
  user-select: none;
  padding: 10px;
  @media screen and (max-width: 400px) {
    width: 340px;
  }

  .mmenu_header {
    display: flex;
    align-items: center;
    padding: 5px;
    border-radius: 10px;
    gap: 10px;
    img {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      object-fit: cover;
    }
    .mmenu_col {
      display: flex;
      flex-direction: column;
      justify-content: center;
      span:first-of-type {
        color: var(--color-primary);
        font-weight: 600;
        font-size: 15px;
        letter-spacing: 1px;
      }
      span:last-of-type {
        font-size: 14px;
      }
    }
  }
  .mmenu_splitter {
    width: 100%;
    height: 1px;
    background: var(--bg-third);
    margin-top: 5px;
  }
  .mmenu_main {
    padding: 10px;
    margin-top: 5px;
    display: flex;
    align-items: center;
    gap: 5px;
    .mmenu_span1 {
      font-size: 14px !important;
    }
    .mmenu_span2 {
      font-size: 12px !important;
      color: var(--color-secondary);
    }
  }
  .mmenu_item {
    position: relative;
    display: flex;
    align-items: center;
    margin-top: 5px;
    padding: 7px;
    font-weight: 600;
    font-size: 14px;
    border-radius: 10px;
    .rArrow {
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  .absolute_wrap {
    padding: 0 0.3rem;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 360px;
    height: fit-content;
    border-radius: 10px;
    background: var(--bg-primary);
    box-shadow: 2px 2px 2px var(--shadow-1);
    user-select: none;
    padding: 10px;

    .mmenu_item {
      margin-top: 0;
    }
    .absolute_wrap_header {
      display: flex;
      align-items: center;
      gap: 10px;
      font-weight: 700;
      font-size: 24px;
    }

    label {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-left: 50px;
      font-weight: 600;
      padding: 10px;
      cursor: pointer;
      border-radius: 10px;
      input {
        width: 20px;
        height: 20px;
      }
    }
  }
}

/*-------User menu----*/
/*-------Active header----*/
.active_header {
  background: var(--light-blue-color);
  svg {
    fill: var(--blue-color);
  }
}
.active_link {
  background: var(--light-blue-color);
  color: var(--blue-color);
}
/*-------Active header----*/
